/* Reset */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  font-family: inherit;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}

@font-face {
  font-family: "Sora-Regular";
  src: url("../../src/assets/fonts/Sora-Regular.ttf");
}
@font-face {
  font-family: "Sora-Bold";
  src: url("../../src/assets/fonts/Sora-Bold.ttf");
}
@font-face {
  font-family: "Sora-ExtraBold";
  src: url("../../src/assets/fonts/Sora-ExtraBold.ttf");
  font-weight: 800;
}
@font-face {
  font-family: "Sora-SemiBold";
  src: url("../../src/assets/fonts/Sora-SemiBold.ttf");
}
body {
  font-family: "Sora-Regular", sans-serif;
  font-size: 16px;
  color: #71717A;
}